.search-box {
  margin: 0;
  margin-top: 3rem;
  background-color: rgb(255, 255, 255);
  width: 13rem;
  display: flex;
  align-items: center;
  border-radius: 1rem;
  padding: 0.5rem;
}

.search-box > input {
  /* font-size: var(--small-font-size); */
  border: none;
  width: 11rem;
  padding-left: 0.5rem;
}

.search-box > input:focus {
  outline: none;
  border: none;
}

.search-icon {
  width: 1rem;
  padding: 0.2rem;
  margin: 0;
}

.search-icon:hover {
  background-color: red;
}
